<template>
<example-get-spain :my-list="list">

</example-get-spain>
</template>

<script>
import EndPoints from "@/service/EndPoints";
import{reactive,onMounted} from "vue";
import{defineAsyncComponent} from "vue";
const ExampleGetSpain=defineAsyncComponent(()=>import('@/components/main/ExampleGetSpain'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AllStanding",
  components: {ExampleGetSpain},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsEngland()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.standing.standings)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      result,list,
    }
  }
}
</script>

